import { API_KEY } from '../constants/api';

//   ,        promise,    
// (. promise  .then(result => console.log(result.data.data.results)))

class GetDataApi {
    async getData(url) {
        // .         .then,   trycatch
        try {
            //     axios  await, .    ,     
            const response = await axios.get(url, {
                //      axios,   API,    
                params: {
                    apikey: API_KEY,
                    //    
                    limit: 100
                }
            });

            return response.data.data.results;

        } catch (error) {
            console.log(error.message)
            return false;
        }
    }
}

//     
export const getDataApi = new GetDataApi();